<template>
  <div>
    <div>f = (bp- q) / b</div>
    <br />
    <Button style="margin-bottom:20px" type="info" ghost @click="desc = !desc">查看</Button>
    <Button style="margin-bottom:20px;margin-left:40px" type="info" ghost @click="empt">清空</Button>
    <div v-if="desc" style="margin-bottom:20px">
      <div>f：期望值</div>
      <div>b：赔率</div>
      <div>p：胜率</div>
      <div>q：败率</div>
      <br>
      <div>f为0，赌局为公平游戏，不应该下注</div>
      <div>f为负，赌徒处于劣势，更不应该下任何赌注</div>
      <div>f为正，按照凯利公式，赚钱最快，风险最小</div>
    </div>

    <div class="inp">
      <Input placeholder="赔率" style="width:200px" v-model="b1"></Input>
      <Input placeholder="胜率" style="width:200px;margin:0 20px" v-model="b2"></Input>
      <Input placeholder="败率" style="width:200px" v-model="b3" @keyup.enter.native="resu"></Input>
      <Button type="info" ghost style="margin-left:20px" @click="resu">计算</Button>
    </div>

    <div style="margin-top:20px" v-if="flag">
      结果：f = {{msg}}

      <div style="margin:20px 0" v-if="msg>0">
        <Input placeholder="成本" style="width:200px" v-model="b4" @keyup.enter.native="end"></Input>
        <Button style="margin-left:40px" type="info" ghost @click="end">计算</Button>
        <div style="margin-top:20px">money = {{msg2}}</div>
        <br>
        <div style="color:green">( 利润 = {{msg3}} ￥)</div>
      </div>

    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      desc: false,
      b1: "",
      b2: "",
      b3: "",
      msg: "",
      flag: false,
      b4: "",
      msg2: "",
      msg3: "",
    };
  },

  methods: {
    empt() {
      this.b1 = "";
      this.b2 = "";
      this.b3 = "";
      this.flag = false;
    },
    resu() {
      let b = this.b1;
      let p = this.b2;
      let q = this.b3;

      if (!b || !p || !q) return;

      let f = (b * p - q) / b;
      f = Number(f);
      let re = Math.floor(f * 100) / 100;
      this.msg = re;
      this.flag = true;
      this.b4 = "";
      this.msg2 = "";
      this.msg3 = "";
    },
    end() {
      this.msg2 = this.msg * this.b4;
      this.msg3 = this.msg2 * this.b1;
    },
  },
};
</script>
<style scoped>
.inp {
  display: flex;
  justify-content: center;
}
</style>


