<template>
  <div>1
    <transition name="fade" enter-class="fade-in-enter" enter-active-class="fade-in-active" leave-class="fade-out-enter"
      leave-active-class="fade-out-active">
      <p v-show="show">hello</p>
    </transition>
    <div>
      <button @click="toChange">{{msg}}</button>
    </div>
    <slotTest2>
      <div slot="three">i am 3</div>
    </slotTest2>

    <div class="newbox">
      <div class="new">222</div>
    </div>

  </div>
</template>

<script>
import slotTest2 from "./slotTest2";

var mixin = {
  data() {
    return {
      msg_mixins: "mixins",
      msg: "123",
    };
  },
  created: function () {
    console.log("混入对象的钩子被调用");
    this.msg = 321;
  },
};

export default {
  name: "slotTest1",
  components: {
    slotTest2,
  },
  mixins: [mixin],
  data() {
    return {
      show: false,
    };
  },
  methods: {
    toChange() {
      this.show = !this.show;
    },
  },
};
</script>

<style lang="" scoped>
.fade-in-active,
.fade-out-active {
  transition: all 0.5s ease;
}
.fade-in-enter,
.fade-out-active {
  opacity: 0;
}

.newbox {
  background: skyblue;
}
.new {
  width: 200px;
  height: 200px;
  background: pink;
  /* clip-path: inset(50px 10px 10px 50px round 8px); */
  /* clip-path: circle(30% at 100px 100px); */
  /* clip-path: ellipse(40% 30% at 50% 50%); */
  clip-path: polygon(50% 0, 100% 50%, 0 100%);
}
</style>


