<template>
  <div>
    <div style="padding:20px">
      <Form ref="formbig" :model="list" inline>
        <FormItem>
          <Input v-model="list.big" placeholder="输入大球水位"></Input>
        </FormItem>
        <FormItem>
          <Input v-model="list.parameter" placeholder="输入参照物"></Input>
        </FormItem>
        <FormItem>
          <Input v-model="list.small" placeholder="输入小球水位" @keyup.enter.native='enter'></Input>
        </FormItem>
      </Form>
    </div>

    <ul class="resultbox">
      <li v-for="(x,i) in result" :key="i">
        <span :class="compareFirstLine(x.left,'left')">{{x.left}}</span>
        <span>{{x.mid}}</span>
        <span :class="compareFirstLine(x.right,'right')">{{x.right}}</span>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  name: "",
  data() {
    return {
      list: {
        big: "",
        small: "",
        parameter: "",
      },
      result: [],
      firstline: null,
    };
  },
  methods: {
    enter() {
      let list = this.list;
      let obj = {};

      list.big = this.add(list.big);
      list.small = this.add(list.small);

      obj.left = list.big ? this.gl(list.big) : 0;
      obj.mid = list.parameter ? list.parameter : 0;
      obj.right = list.small ? this.gl(list.small) : 0;

      let b = this.result;
      b.push(obj);

      this.firstline = this.result[0];
    },
    gl(e) {
      let n = (1 / e).toFixed(2);
      return n;
    },
    // 小于1的数值
    add(e) {
      let n = e < 1 ? Number(e) + Number(1) : Number(e);
      return n;
    },
    compareFirstLine(e, pos) {
      let d = this.firstline;
      if (d == null) {
        return "";
      }

      if (d[pos] > e) {
        return "green";
      }

      if (d[pos] < e) {
        return "red";
      }
    },
  },
};
</script>

<style lang="" scoped>
.resultbox li span {
  display: inline-block;
  width: 120px;
  padding: 4px;
}

.red {
  color: red;
}
.green {
  color: green;
}
</style>


