import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import moni3 from '@/components/moni3'
import moni4 from '@/components/moni4'
import moniall from '@/components/moniall'
import HuaDong from '@/pages/huadong'
import water from '@/pages/water'
import kl from '@/pages/kl'
import slotTest1 from '@/pages/slotTest1'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'kl',
      component: kl
    },
    {
      path: '/water',
      name: 'water',
      component: water
    },
    {
      path: '/HuaDong',
      name: 'HuaDong',
      component: HuaDong
    },
    {
      path: '/football',
      name: 'HelloWorld',
      component: HelloWorld
    },
    {
      path: '/m3',
      name: 'moni3',
      component: moni3
    },
    {
      path: '/m4',
      name: 'moni4',
      component: moni4
    },
    {
      path: '/moniall',
      name: 'moniall',
      component: moniall
    },
  ]
})



