<template>
  <div class="box">
    <div style="text-align:left">
      <span>name：</span>
      <Input placeholder="name" style="width:300px"></Input>
    </div>

    <div class="atline">
      <!-- 主队 -->
      <div class="bho">
        <div class="title">
          <span>HOST</span>
        </div>
        <Form ref="host" :model="host" :label-width="60" class="hbox">
          <FormItem label="attack：">
            <Input v-model="host.attack" ref="firstinp"></Input>
          </FormItem>
          <FormItem label="danger：">
            <Input v-model="host.danger"></Input>
          </FormItem>
          <FormItem label="shot：">
            <Input v-model="host.shot"></Input>
          </FormItem>
          <FormItem label="shotIn：">
            <Input v-model="host.shotIn"></Input>
          </FormItem>
        </Form>
      </div>

      <!-- 客队 -->
      <div class="bho">
        <div class="title">
          <span>GUEST</span>
        </div>
        <Form ref="guest" :model="guest" :label-width="60" class="hbox">
          <FormItem label="attack：">
            <Input v-model="guest.attack"></Input>
          </FormItem>
          <FormItem label="danger：">
            <Input v-model="guest.danger" @on-enter="doclick"></Input>
          </FormItem>
          <FormItem label="shot：">
            <Input v-model="guest.shot"></Input>
          </FormItem>
          <FormItem label="shotIn：">
            <Input v-model="guest.shotIn"></Input>
          </FormItem>
        </Form>
      </div>

      <div class="hend">
        <Input v-model="common.time" placeholder="please input cerrent time" class="doinp"></Input>
        <Button type="default" class="doit" @click="doclick">doIt</Button>
      </div>
    </div>

    <Table :columns="column" :data="data" ref="d1"></Table>

    <div class="empty"></div>

    <Table :columns="column" :data="data2" ref="d2"></Table>
  </div>
</template>

<script>
export default {
  name: "",
  data() {
    return {
      column: [
        {
          title: "Name",
          key: "name",
        },
        {
          title: "Time",
          key: "time",
          render: (h, params) => {
            let row = params.row;
            let time = row.time + ":00";
            return h("span", time);
          },
        },
        // {
        //   title: "TimeGo",
        //   key: "TimeGo",
        //   render: (h, params) => {
        //     let row = params.row;
        //     let index = params.index;
        //     let time;
        //     if (index == 0) {
        //       time = 0;
        //     } else {
        //       time = row.time - this.data[index - 1].time + "min";
        //     }
        //     return h("span", time);
        //   },
        // },
        // {
        //   title: "Attack",
        //   key: "attack",
        //   render: (h, params) => {
        //     console.log("data -> params", params);
        //     let row = params.row;
        //     let index = params.index;
        //     let att;
        //     if (index == 0) {
        //       att = row.attack;
        //     } else {
        //       if (row.name == "host") {
        //         att = row.attack - this.data[index - 1].attack;
        //       } else {
        //         att = row.attack - this.data2[index - 1].attack;
        //       }

        //       att = "+" + att;
        //     }
        //     return h("span", att);
        //   },
        // },
        // {
        //   title: "Danger",
        //   key: "danger",
        //   render: (h, params) => {
        //     let row = params.row;
        //     let index = params.index;
        //     let dan;
        //     if (index == 0) {
        //       dan = row.danger;
        //     } else {
        //       if (row.name == "host") {
        //         dan = row.danger - this.data[index - 1].danger;
        //       } else {
        //         dan = row.danger - this.data2[index - 1].danger;
        //       }

        //       dan = "+" + dan;
        //     }
        //     return h("span", dan);
        //   },
        // },
        {
          title: "Effect",
          key: "effect",
          render: (h, params) => {
            let row = params.row;

            let e = row.danger / row.attack;
            let e2 = row.shotIn / row.shot;
            let temp = (e + e2) / 2;

            temp = ((temp * 1000) / 1000) * 100;
            temp = Number(temp).toFixed(2) + "%";
            return h(
              "span",
              {
                style: {
                  fontSize: "16px",
                  color: "green",
                },
              },
              temp
            );
          },
        },
        // {
        //   title: "ShotEffect",
        //   key: "shoteffect",
        //   render: (h, params) => {
        //     console.log(params, "111");
        //     let row = params.row;
        //     let e = row.shotIn / row.shot;
        //     e = Math.floor(e * 1000) / 1000;
        //     e = e * 100;
        //     e = Number(e).toFixed(2) + "%";
        //     return h(
        //       "span",
        //       {
        //         style: {
        //           fontSize: "16px",
        //           color: "gray",
        //         },
        //       },
        //       e
        //     );
        //   },
        // },
        {
          title: "Option",
          key: "option",
          render: (h, params) => {
            let row = params.row;
            let ind = params.index;

            return h(
              "i-button",
              {
                on: {
                  click: () => {
                    this.data.splice(ind, 1);
                    this.data2.splice(ind, 1);
                  },
                },
              },
              "删除"
            );
          },
        },
      ],
      data: [],
      data2: [],
      common: {
        time: "",
      },
      host: {
        attack: "",
        danger: "",
        shot: "",
        shotIn: "",
      },
      guest: {
        attack: "",
        danger: "",
        shot: "",
        shotIn: "",
      },
    };
  },
  methods: {
    doclick() {
      let hosto = {};
      hosto.name = "host";
      hosto.attack = this.host.attack;
      hosto.danger = this.host.danger;
      hosto.shot = this.host.shot;
      hosto.shotIn = this.host.shotIn;
      hosto.time = this.common.time;
      let l = this.data.length;
      this.$set(this.data, l, hosto);

      let guesto = {};
      guesto.name = "guest";
      guesto.attack = this.guest.attack;
      guesto.danger = this.guest.danger;
      guesto.shot = this.guest.shot;
      guesto.shotIn = this.guest.shotIn;
      guesto.time = this.common.time;
      let l2 = this.data2.length;
      this.$set(this.data2, l2, guesto);

      // 初始化
      this.host.attack = "";
      this.host.danger = "";
      this.host.shot = "";
      this.host.shotIn = "";
      this.guest.attack = "";
      this.guest.danger = "";
      this.guest.shot = "";
      this.guest.shotIn = "";

      this.$refs["firstinp"].focus();
    },
  },
};
</script>

<style lang="" scoped>
.box {
  width: 70%;
  margin: 20px;
  padding: 20px;
}

.atline {
  display: flex;
  flex-direction: column;
}

.hbox {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.hbox .ivu-form-item {
  width: 48%;
}

.bho {
  width: 99%;
  border: 1px solid #cdcdcd;
  padding: 12px;
  margin: 12px;
}

.bho .title {
  text-align: left;
  padding-bottom: 5px;
  margin-bottom: 5px;
  border-bottom: 1px dashed #cdcdcd;
}

.doinp {
}
.doit {
  width: 160px;
  margin-left: 20px;
}
.hend {
  padding: 20px 0;
  display: flex;
}

.empty {
  height: 30px;
}
</style>


