<template>
  <div>
    <RecycleScroller
      class="scroller"
      :items="list"
      :item-size="30"
      key-field="id"
      :buffer="50"
      v-slot="{ item }"
    >
      <div class="user">{{ item }}</div>
    </RecycleScroller>

    <!-- <RecycleScroller class="scroller" :items="list" :item-size="32">
      <template #before>Hey! I'm a message displayed before the items!</template>

      <template v-slot="{ item }">
        <div class="user">{{ item }}</div>
      </template>
    </RecycleScroller>-->
  </div>
</template>

<script>
export default {
  data() {
    return {
      list: []
    };
  },
  created() {
    this.getNums();
  },
  methods: {
    getNums() {
      let arr = [];
      for (let i = 0; i < 1000; i++) {
        arr.push(i);
      }
      this.list = arr;
    }
  }
};
</script>

<style lang="" scoped>
.scroller {
  height: 400px;
}

.user {
  height: 32%;
  padding: 0 12px;
  display: flex;
  align-items: center;
}
</style>


