<template>
  <div>
    <div @keyup.enter="suan" class="dk">
      <div v-for="(v, k) in lion" :key="k">
        <Input v-model="lion[k]"
          ><span slot="prepend">{{ k }}</span></Input
        >
      </div>
    </div>

    <div>
      <ul v-for="(x, i) in arr" :key="i">
        <li>时间：<Time :time="time" type="datetime" /></li>
        <li>j1效率：{{ x.a1 }}</li>
        <li>j2效率：{{ x.a2 }}</li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  name: "",
  data() {
    return {
      time: new Date(),
      lion: {
        n: "", // 攻击次数
        c: "", // 危险次数
        p: "", // 偏离
        z: "", // 正中
      },
      arr: [],
    };
  },
  methods: {
    suan() {
      let o = this.lion;
      let tmp = {};
      tmp.a1 = (Number(o.c) / o.n).toFixed(1);
      tmp.a2 = (Number(o.z) / (Number(o.p) + Number(o.z))).toFixed(1);
      this.arr.push(tmp);
    },
  },
};
</script>

<style lang="" scoped>
.dk > div {
  margin: 8px;
}
ul {
  padding: 10px;
  border: 1px solid #cdcdcd;
}
ul li {
  text-align: left;
}
</style>


