<template>
  <div style="width: 47%; padding: 14px">
    <Input v-model="changci" @on-blur="getChangci"
      ><span slot="prepend">查询最近场次数</span></Input
    >
    <div style="text-align: left">
      <div v-for="(x, x1) in changci" :key="x1" style="margin-bottom: 20px">
        <li>
          <span>{{ x1 + 1 }}：</span>
          <i-switch v-model="aa[x1]" @on-change="getList"></i-switch>
        </li>
      </div>
      <Input v-model="ping" v-show="title == '胜负' || title == '历史'"
        ><span slot="prepend">平局</span></Input
      >
      <i-button
        type="success"
        style="width: 100px; margin-top: 10px"
        @click="hua"
        >画图</i-button
      >
    </div>

    <div :id="id" style="width: 600px; height: 200px"></div>
  </div>
</template>

<script>
import echarts from "echarts";
export default {
  name: "",
  data() {
    return {
      ping: "",
      changci: 6,
      arr: [],
      id: "",
      title: "",
    };
  },
  props: ["vers", "titl"],
  mounted() {
    this.id = this.vers;
    this.title = this.titl;
  },
  computed: {
    aa: function () {
      let arr = [];
      for (let i = 0; i < this.changci; i++) {
        arr[i] = false;
      }

      this.arr = arr;
      return arr;
    },
  },
  methods: {
    getChangci() {
      this.changci = Number(this.changci);
    },
    getList() {},
    hua() {
      let v = [];
      v = JSON.parse(JSON.stringify(this.arr));
      for (let i = 0; i < v.length; i++) {
        if (v[i] == false) {
          v[i] = 0;
        }
        if (v[i] == true) {
          v[i] = 1;
        }
      }

      if (this.title == "历史" || this.title == "胜负") {
        let tmp = this.ping.split(",");
        console.log("hua -> tmp", tmp);
        for (let t = 0; t < tmp.length; t++) {
          v[tmp[t] - 1] = 0.5;
        }
      }

      v = v.reverse();
      console.log(v);

      var myChart = echarts.init(document.getElementById(this.id));
      myChart.clear();

      // 指定图表的配置项和数据
      var option = {
        title: {
          text: this.title,
          textStyle: {
            //主标题文本样式{"fontSize": 18,"fontWeight": "bolder","color": "#333"}
            fontFamily: "Arial, Verdana, sans...",
            fontSize: 14,
            fontStyle: "normal",
            fontWeight: "normal",
          },
          x: "center",
          textAlign: "center",
        },
        xAxis: {
          type: "category",
          data: v,
        },
        yAxis: {
          max: 1,
          splitNumber: 2,
          splitLine: {
            show: false,
          },
        },
        tooltip: {
          trigger: "axis",
        },
        series: [
          {
            data: v,
            type: "line",
            smooth: true,
          },
        ],
      };

      // 使用刚指定的配置项和数据显示图表。
      myChart.setOption(option);
    },
  },
};
</script>

<style lang="" scoped>
li {
  list-style-type: none;
  margin: 6px 0;
}
</style>


