<template>
  <div class="aaa">
    <div class="tops">
      <Checkbox v-model="ls" size="large">历史</Checkbox>
      <Checkbox v-model="sf" size="large">胜负</Checkbox>
      <Checkbox v-model="dx" size="large">大小</Checkbox>
    </div>

    <div class="box box1" v-show="ls">
      <Moni4 vers="m0" titl="历史"></Moni4>
    </div>

    <div class="box" v-show="sf">
      <Moni4 vers="m1" titl="胜负"></Moni4>
      <Moni4 vers="m2" titl="胜负"></Moni4>
    </div>

    <div class="box" v-show="dx">
      <Moni4 vers="m3" titl="大小"></Moni4>
      <Moni4 vers="m4" titl="大小"></Moni4>
    </div>
  </div>
</template>

<script>
import Moni4 from "@/components/moni4";
export default {
  name: "",
  data() {
    return {
      dx: false,
      sf: false,
      ls: true,
    };
  },
  components: {
    Moni4,
  },
};
</script>

<style lang="" scoped>
.box {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  border: 1px dotted;
  margin: 7px 0;
}

.tops {
  width: 84%;
  margin: 0 auto;
  text-align: left;
}

.aaa {
  margin: 0 40px;
}
</style>


